<script setup>
    import {ref} from 'vue'
    import ChannelSelect from './ChannelSelect.vue'
    import { Plus } from '@element-plus/icons-vue'
    import { QuillEditor } from '@vueup/vue-quill'
    import '@vueup/vue-quill/dist/vue-quill.snow.css'
    import { artPublishService,artGetDetailService,artEditDetailService } from '@/api/article'
    import { ElMessage } from 'element-plus'
    import { baseURL } from '@/utils/request'
    import axios from 'axios'
    
    //
    const visibleDrawer = ref(false)

    //
    const defaultForm = {
        title:'',//
        cate_id:'',//id
        cover_img:'',// file 
        content:'',//string
        state:''//
    }


    //
    const formModel = ref({
        ...defaultForm
    })
    //
    const imgUrl = ref('')
    const onSelectFile = (file)=>{
        imgUrl.value = URL.createObjectURL(file.raw) //
        //formModel.value.cover_img
        formModel.value.cover_img = file.raw

    }
    //
    const emit = defineEmits(['success'])
    const onPublish = async (state) =>{
        formModel.value.state = state
        //formData
        //formData
        const fd = new FormData()
        for(let key in formModel.value){
            fd.append(key,formModel.value[key])
        }

        //
        if(formModel.value.id){
            //
            await artEditDetailService(fd)
            ElMessage.success('')
            visibleDrawer.value = false
            emit('success','edit')
        }else{
            //
            await artPublishService(fd)
            ElMessage.success('')
            visibleDrawer.value = false
            emit('success','add')

        }
    }
    //File
    async function imageUrlToFileObject(imgUrl,filename){
        try{
            //Axios
            const response = await axios.get(imgUrl,{responseType:'arraybuffer'})

            const blob = new Blob([response.data],{
                type:response.headers['content-type']
            })

            const file = new File([blob],filename,{
                type:response.headers['content-type']
            })

            return file

        }catch(error){
            console.error('Error converting image URL to File object',error)
            return null
        }
    }

    const editorRef = ref()
    const open = async (row) =>{
        visibleDrawer.value = true //
        if(row.id){
            //row.id
            const res = await artGetDetailService(row.id)
            formModel.value = res.data.data
            //
            imgUrl.value = baseURL + formModel.value.cover_img
            //file
            //file
            const file = await imageUrlToFileObject(imgUrl.value,formModel.value.cover_img)
            formModel.value.cover_img = file
        }else{
            formModel.value = {...defaultForm} //form
            //img
            imgUrl.value = ''
            editorRef.value.setHTML('')
        }
    }

    defineExpose({
        open
    })
</script>




<template>
    <el-drawer
        v-model="visibleDrawer"
        title=""
        direction="rtl"
        size="50%"
     >
        <el-form :model="formModel" ref="formRef" label-width="100px">
            <el-form-item label="" prop="title">
                <el-input v-model="formModel.title" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="cate_id">
                <channel-select
                v-model="formModel.cate_id"
                width="100%"
                ></channel-select>
            </el-form-item>
            <el-form-item label="" prop="cover_img">
                <!-- element-plusaction
                 
                 URL.createObjectURL(...) -->
                <el-upload
                class="avatar-uploader"
                :show-file-list="false"
                :auto-upload="false"
                :on-change="onSelectFile"
                >
                    <img :src="imgUrl" v-if="imgUrl" class="avatar">
                    <el-icon v-else class="avatar-uploader-icon"><Plus /></el-icon>
                </el-upload>
            </el-form-item>
            <el-form-item label="" prop="content">
                <div class="editor">
                    <QuillEditor 
                    ref="editorRef"
                    theme="snow"
                    v-model:content="formModel.content"
                    content-type="html"
                    ></QuillEditor>
 
                </div>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onPublish('')"></el-button>
                <el-button type="info" @click="onPublish('')"></el-button>
            </el-form-item>

        </el-form>
     </el-drawer>
</template>

<style lang="scss" scoped>
    .avatar-uploader{
    :deep(){
        .avatar{
            width: 178px;
            height: 178px;
            display: block;
        }
        .el-upload{
            border:1px dashed var(--el-border-color);
            border-radius:6px;
            cursor:pointer;
            position:relative;
            overflow: hidden;
            transition:var(--el-transition-duration-fast);
        }
        .el-upload:hover{
            border-color:var(--el-color-primary);
        }
        .el-icon.avatar-uploader-icon{
            font-size:28px;
            color:#8c939d;
            width: 178px;
            height: 178px;
            text-align: center;
        }
    }
    
}
.editor{
        width: 100%;
        :deep(.ql-editor){
            min-height: 200px;
        }
    }
</style>