<script setup>
    import {Edit,Delete} from '@element-plus/icons-vue'
    import {ref} from 'vue'
    import ChannelSelect from './components/ChannelSelect.vue'
    import { artGetListService } from '@/api/article'
    import {formatTime} from '@/utils/format.js'
    import ArticleEdit from './components/ArticleEdit.vue'
    import { ElMessage } from 'element-plus'
    const articleList = ref([])//
    const total = ref(0)//
    const loading = ref(false)
    
   
    //
    const params= ref({
        pagenum:1,//
        pagesize:5,//
        cate_id:'',
        state:''
    })
    //params
    const getArticleList = async () =>{
        loading.value = true
        const res = await artGetListService(params.value)
        articleList.value = res.data.data
        total.value = res.data.total

        loading.value = false
    }
    getArticleList()
    //
    const onEditArticle = (row)=>{
        articleEditRef.value.open(row)
    }
    //
    const onDelArticle = ()=>{
        ElMessage.error('')
    }

    //
    const onSizeChange = (size)=>{
        //
        params.value.pagenum = 1
        params.value.pagesize = size

        getArticleList()

    }
    const onCurrentChange = (page)=>{
        params.value.pagenum = page

        getArticleList()

    }
  

    // => 
    const onSearch = ()=>{
        params.value.pagenum = 1
        getArticleList()
    }

    const onReset = ()=>{
        params.value.cate_id = ''
        params.value.state= ''
        params.value.pagenum = 1

    }
    //
    const articleEditRef = ref()

    const onAddArticle = ()=>{
    articleEditRef.value.open({})
}
    //
    const onSuccess = (type)=>{
        if(type === 'add'){
            //
            const lastPage = Math.ceil((total.value+1) / params.value.pagesize)
            //
            params.value.pagenum = lastPage
            

        }else{
            //
            

        }
        getArticleList()
    }

</script>


<template>
    <page-container title="">
        <template #extra>
            <el-button type="primary" @click="onAddArticle"></el-button>
        </template>

            <!--  -->
            <el-form inline>
                <el-form-item label="">
                    <ChannelSelect v-model="params.cate_id" width="220px"></ChannelSelect>
                </el-form-item>
                <el-form-item label="">
                    <!--  -->
                    <el-select style="width:220px" v-model="params.state">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>

                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSearch"></el-button>
                    <el-button @click="onReset"></el-button>
                </el-form-item>
            </el-form>

            <!--  -->
             <el-table :data="articleList" v-loading="loading">
                <el-table-column label="" prop="title" >
                    <template #default="{row}">
                        <el-link type="primary" :underline="false">{{ row.title }}</el-link>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="cate_name"></el-table-column>
                <el-table-column label="" prop="pub_date">
                    <template #default="{row}">
                        {{ formatTime(row.pub_date) }}
                    </template>
                </el-table-column>
                <el-table-column label="" prop="state"></el-table-column>
                <el-table-column label="">
                    <!-- row -->
                    <template #default="{row}">
                        <el-button circle plain type="primary" :icon="Edit" @click="onEditArticle(row)" ></el-button>
                        <el-button circle plain type="danger" :icon="Delete" @click="onDelArticle()"></el-button>
                        
                    </template>
                </el-table-column>
             </el-table>

             <!--  -->
              <el-pagination
                v-model:current-page="params.pagenum"              
                v-model:page-size="params.pagesize"
                :page-sizes="[2,3,5,10]"
                :background="true"
                layout="jumper,total,sizes,prev,pager,next"
                :total="total"
                @size-change="onSizeChange"
                @current-change="onCurrentChange"
                style="margin-top:20px; justify-content: flex-end;"
              />

              <!--  -->
             <article-edit ref="articleEditRef" @success="onSuccess"></article-edit>
                
              
    </page-container>     
</template>

<style lang="scss" scoped>

</style>